export type FAQItem = {
  answer: string
  question: string
}

export type FAQList = {
  faqList: FAQItem[]
}

export const isFAQItem = (obj: any): obj is FAQItem => {
  return 'answer' in obj && 'question' in obj
}

export const isFAQList = (obj: any): obj is FAQList => {
  return 'faqList' in obj && obj.faqList.isArray()
}
