import React, { FC } from 'react'
import ReactMarkdown from 'react-markdown'
import * as LidoUI from '@lidofinance/lido-ui'
const { Accordion } = LidoUI

import { FAQList } from './types'
import { FaqItem } from './styles'

export const FaqAccordion: FC<FAQList> = ({ faqList }) => {
  return (
    <>
      {faqList.map(({ question, answer }, index) => (
        <Accordion key={question} defaultExpanded={index === 0} summary={String(question)}>
          <FaqItem>
            <ReactMarkdown>{answer}</ReactMarkdown>
          </FaqItem>
        </Accordion>
      ))}
    </>
  )
}
