// eslint-disable-next-line import/no-extraneous-dependencies
import axios, { Axios } from 'axios'
import { unified, PluggableList } from 'unified'
import remarkParse from 'remark-parse'
import remarkStringify from 'remark-stringify'
import remarkFrontmatter from 'remark-frontmatter'
import remarkDirective from 'remark-directive'

import { FAQItem } from '../components'
import { getFrontmatter } from './remarkPlugins'

export const parseYamlFileWithMdContent = async (
  md: string,
  remarkPlugins?: PluggableList,
): Promise<{ content: string; data: Record<string, any> }> => {
  const file = await unified()
    .use(remarkParse)
    .use(remarkStringify)
    .use(remarkPlugins || [])
    .process(md)

  return { content: String(file), data: file.data }
}

export type PageFAQ = { identification: string; faq: FAQItem[] }

export const isPageFAQ = (obj: any): obj is PageFAQ => {
  return 'identification' in obj && 'faq' in obj
}

export const getFAQ = async (faqUrl: string, axiosServerInstance?: Axios | undefined): Promise<PageFAQ[]> => {
  let rawData;

  if (axiosServerInstance) {
    rawData = (await axiosServerInstance.get<string>(faqUrl)).data;
  } else {
    rawData = (await axios.get<string>(faqUrl)).data;
  }

  // Temporary solution
  // The CMS, while generating md, adds an '\_' between the text and
  // the link, which is then replaced by 'nbsp;' (non-breaking space)
  // and interferes correct content display
  // To fix this, we replace '\_' with ' '
  const fixedFileContent = rawData.replace(/\\_\[/gm, ' [').replace(/\)\\_/gm, ') ')

  const { data } = await parseYamlFileWithMdContent(fixedFileContent, [
    remarkDirective,
    [remarkFrontmatter, ['yaml']],
    getFrontmatter,
  ])
  const { frontmatter = {} } = data
  const { pages = [] } = frontmatter
  return pages
}
